}

tobago-bar.navbar-dark .navbar-brand > .tobago-link:hover, tobago-bar.navbar-dark .navbar-brand > .tobago-link:focus {
  text-decoration: none;
}

tobago-behavior {
  display: none;
}

/* box -------------------------------------------------------------- */
tobago-box {
  margin-bottom: 1rem;
}

tobago-box > .card-body {
  overflow-x: hidden;
  overflow-y: auto;
}

.tobago-box-header {
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.card-header > h3:first-child, .card-header > .h3:first-child {
  margin-bottom: 0;
  align-self: center;
}

.card-header .navbar {
  padding: 0;
}

/* buttons, links ------------------------------------------------------- */
tobago-buttons > .tobago-button {
  display: inline-block;
}

/* button, link ------------------------------------------------------- */
.tobago-button > img + span, .tobago-link > img + span {
  margin-left: .4em;
}

.tobago-button > i + span, .tobago-link > i + span {
  margin-left: .4em;
}

/* button -------------------------------------------------------------- */
/* XXX for what is this needed?
a.tobago-button {
  &.btn-primary:focus {
    color: $btn-primary-color;
  }
  &.btn-secondary:focus {
    color: $btn-secondary-color;
  }
}
*/
button.tobago-button:disabled {
  cursor: not-allowed;
}

button.tobago-button:disabled img, button.tobago-button:disabled span {
  pointer-events: none;
}

/* collapsible -------------------------------------------------------------- */
tobago-box.tobago-collapsed .card-body,
tobago-section.tobago-collapsed .tobago-section-content,
tobago-panel.tobago-collapsed {
  display: none;
}

/* date -------------------------------------------------------------- */
tobago-date {
  display: block;
}

tobago-date input {
  min-width: 7em;
}

tobago-date input::-ms-clear {
  /* Remove IE10's "clear field" X button */
  display: none;
}

tobago-date input:disabled {
  color: rgba(85, 89, 92, 0.5);
}

/* for pickers with more than one icon, e.g. date-time picker */
.btn.tobago-date-picker > .fa:nth-child(n+2) {
  margin-left: .3em;
}

/*
workaround for Bootstrap Modal (Popup) with vanillajs-datepicker
*/
.datepicker-dropdown {
  z-index: 2000 !important;
}

/* figure -------------------------------------------------------------- */
/* file -------------------------------------------------------------- */
tobago-file {
  display: block;
}

/* flexLayout -------------------------------------------------------------- */
tobago-flex-layout {
  display: flex;
  min-width: 0;
  /* without this, Firefox/Webkit are different from IE:
     Set the minimal width to zero make flex-layout responsive for the width,
     even if there is overflowing content.
     currently e.g. PRE-code blocks in the demo are too wide. XXX */
  margin-left: -3px;
  margin-right: -3px;
}

tobago-flex-layout > * {
  margin-left: 3px;
  margin-right: 3px;
}

tobago-flex-layout > tobago-flex-layout {
  margin-left: 0;
  margin-right: 0;
}

/* the non-label-element inside of a label-layout with flex box */
.tobago-label-container {
  display: flex;
}

.tobago-label-container > label {
  width: 155px;
}

.tobago-label-container > .form-control, .tobago-label-container > .form-control-plaintext,
.tobago-label-container > .tobago-input-group-outer, .tobago-label-container > .tobago-messages-container,
.tobago-label-container > .tobago-selectManyShuttle {
  flex: 1 0 0px;
}

/* flowLayout ---------------------------------------------------------- */
/* focus --------------------------------------------------------------- */
tobago-focus {
  display: none;
}

/* footer -------------------------------------------------------------- */
tobago-footer {
  display: block;
}

tobago-footer {
  background-color: #ffffff;
  padding: 5px 10px;
  box-shadow: 0 0 5px 5px rgba(200, 200, 200, 0.5);
}

.fixed-bottom {
  margin-top: 10px;
}

/* form -------------------------------------------------------------- */
/* gridLayout -------------------------------------------------------------- */
tobago-grid-layout {
  display: -ms-grid;
  display: grid;
  border-spacing: 0;
  border-collapse: collapse;
  grid-column-gap: 1rem;
  column-gap: 1rem;
}

tobago-grid-layout .tobago-label-container > .tobago-textarea, tobago-grid-layout .tobago-label-container > .tobago-selectManyListbox, tobago-grid-layout .tobago-label-container > .tobago-selectManyShuttle {
  height: 100%;
}

/* header ----------------------------------------------------------- */
tobago-header {
  display: block;
}

tobago-header {
  margin-bottom: 1rem;
}

tobago-header.sticky-top {
  margin-top: -1rem;
  margin-left: -0.75rem;
  margin-right: -0.75rem;
}

/* hidden ----------------------------------------------------------- */
.tobago-hidden:disabled {
  display: none;
}

/* image ----------------------------------------------------------- */
.tobago-image.disabled {
  filter: grayscale(1) blur(2px) contrast(0.5) brightness(1.2);
}

/* in ----------------------------------------------------------- */
tobago-in {
  display: block;
}

.tobago-in:disabled {
  color: rgba(85, 89, 92, 0.5);
}

.tobago-in-markup-number {
  text-align: right;
}

/* label ----------------------------------------------------------- */
.tobago-label {
  color: #6610f2 !important;
}

/* link ----------------------------------------------------------- */
.tobago-link {
  white-space: nowrap;
}

.tobago-link span {
  white-space: normal;
}

.tobago-link.nav-link:focus, .tobago-link.nav-link:hover {
  text-decoration: none;
}

a.tobago-link.dropdown-item img, a.tobago-link.dropdown-item span {
  display: inline-block;
  /* fix for IE11 - :active selector doesn't work for nested elements */
  pointer-events: none;
}

h1 button.tobago-link, .h1 button.tobago-link, h2 button.tobago-link, .h2 button.tobago-link, h3 button.tobago-link, .h3 button.tobago-link, h4 button.tobago-link, .h4 button.tobago-link, h5 button.tobago-link, .h5 button.tobago-link, h6 button.tobago-link, .h6 button.tobago-link {
  font-weight: inherit;
}

button.tobago-link {
  color: #0d6efd;
  border-width: 0;
  padding: 0;
  background-color: transparent;
  text-align: left;
}

button.tobago-link:focus, button.tobago-link:hover {
  color: #8a5318;
  cursor: pointer;
  outline: none;
  text-decoration: underline;
}

button.tobago-link:disabled {
  color: #d7d7d7;
}

button.tobago-link:disabled:hover {
  cursor: not-allowed;
  text-decoration: none;
}

button.tobago-link:disabled > img {
  opacity: .65;
}

button.tobago-link.dropdown-item {
  padding: 0.25rem 1rem;
  color: #212529;
  text-align: inherit;
  white-space: nowrap;
}

button.tobago-link.dropdown-item:active {
  color: #fff;
}

button.tobago-link.dropdown-item:focus, button.tobago-link.dropdown-item:hover {
  cursor: pointer;
  text-decoration: none;
}

button.tobago-link.dropdown-item:disabled {
  color: #788c94;
}

button.tobago-link.dropdown-toggle:focus, button.tobago-link.dropdown-toggle:hover {
  text-decoration: none;
}

button.nav-link {
  /* bootstrap don't know button.nav-link*/
  padding: 0.55em 1rem 0.45em;
}

.navbar-nav button.nav-link {
  padding-left: 0;
  padding-right: 0;
}

span.dropdown {
  display: inline-block;
}

.dropdown > .btn.dropdown-toggle {
  width: 100%;
}

/* messages / help text ----------------------------------------------- */
.tobago-messages-container {
  display: flex;
  align-items: flex-start;
}

.tobago-messages-container .tobago-messages:first-child small label, .tobago-messages-container .tobago-messages:first-child .small label {
  margin-right: 0;
  /* for tc:in margin 5px is already set */
  margin-bottom: 5px;
}

.tobago-popover-box {
  display: none;
  width: max-content;
}

.tobago-popover-box.show {
  display: block;
}

a.tobago-messages-button, a.tobago-help-button, a.tobago-popover-button {
  padding-left: 0.4em;
  padding-right: 0.4em;
}

.tobago-messages label {
  display: block;
}

.tobago-messages label:last-child {
  margin-bottom: 0;
}

tobago-flex-layout.tobago-messages-container > .tobago-input-group-outer, tobago-flex-layout.tobago-messages-container > .tobago-selectManyShuttle {
  flex: 1 0 0px;
}

.tobago-messages .alert.alert-dismissible {
  padding-left: 2.6rem;
}

.tobago-messages .alert.alert-dismissible:before {
  font-family: FontAwesome;
  position: absolute;
  left: 0;
  padding-left: 0.8rem;
  padding-right: 0.8rem;
  width: 2.6rem;
  text-align: center;
}

.tobago-messages .alert.alert-dismissible.alert-danger:before {
  content: "";
}

.tobago-messages .alert.alert-dismissible.alert-warning:before {
  content: "";
}

.tobago-messages .alert.alert-dismissible.alert-info:before {
  content: "";
}

.popover .popover-body {
  white-space: pre-line;
}

/* nav ----------------------------------------------------------- */
/* styles for drop down menu first level */
.dropdown-toggle:focus, .dropdown-toggle:hover {
  text-decoration: none;
}

/* no bottom border in navtabs, if panel-body under it */
.nav-tabs {
  border-bottom-width: 0;
}

.nav-tabs > li.active > a {
  background-image: linear-gradient(to bottom, #f5f5f5 0%, #e8e8e8 100%);
  background-repeat: repeat-x;
  border-bottom: #dddddd;
}

.nav-tabs > li.active > a:hover {
  border-bottom: #dddddd;
}

.dropdown-menu .form-check {
  display: block;
}

/* dropdown ------------------------------------------------------- */
tobago-dropdown {
  display: inline-block;
}

tobago-dropdown .tobago-dropdown-selected, .tobago-page-menuStore .tobago-dropdown-selected {
  color: #16181b;
  text-decoration: none;
  background-color: #f7f7f7;
}

ul > tobago-dropdown {
  display: list-item;
}

.dropdown-menu .form-check {
  padding-left: 0;
}

.dropdown-menu .form-check .form-check-label.dropdown-item {
  padding-left: 3.5rem;
}

.dropdown-menu .form-check .form-check-label.dropdown-item:before {
  top: 0.5rem;
  left: 1rem;
}

.dropdown-menu .form-check .form-check-label.dropdown-item:after {
  top: 0.5rem;
  left: 1rem;
}

/* dropdown-submenu ------------------------------------------------------- */
.tobago-dropdown-submenu {
  cursor: pointer;
  position: relative;
  width: 100%;
}

.tobago-dropdown-submenu:active > button.tobago-link {
  color: #fff;
}

.tobago-dropdown-submenu > .dropdown-menu {
  top: 0;
  left: 100%;
  margin-top: -6px;
  margin-left: -1px;
  border-radius: 6px 6px 6px 6px;
}

.tobago-dropdown-submenu > button:after {
  display: none;
}

.tobago-dropdown-submenu > button.tobago-link {
  color: #212529;
}

.tobago-dropdown-submenu > button.tobago-link:focus, .tobago-dropdown-submenu > button.tobago-link:hover {
  text-decoration: none;
}

.tobago-dropdown-submenu:after {
  content: " ";
  float: right;
  border-color: transparent;
  border-style: solid;
  border-width: 5px 0 5px 5px;
  border-left-color: #cccccc;
  margin-top: 7px;
  margin-right: -10px;
}

.tobago-dropdown-submenu:hover > .dropdown-menu, .tobago-dropdown-submenu.tobago-dropdown-open > .dropdown-menu {
  display: block;
}

.tobago-dropdown-submenu:hover > .dropdown-menu > a:after, .tobago-dropdown-submenu.tobago-dropdown-open > .dropdown-menu > a:after {
  border-left-color: #fff;
}

.tobago-dropdown-submenu.pull-left {
  float: none;
}

.tobago-dropdown-submenu.pull-left > .dropdown-menu {
  left: -100%;
  margin-left: 10px;
  border-radius: 6px 0 6px 6px;
}

/* object ----------------------------------------------------------------- */
/* out -------------------------------------------------------------------- */
tobago-out {
  display: inline-block;
}

.tobago-out-markup-strong {
  font-weight: bold;
}

.tobago-out-markup-deleted {
  text-decoration: line-through;
}

.tobago-out-markup-number {
  display: block;
  text-align: right;
}

/* make sure, tc:out has always the same height, no matter if value is empty */
span.tobago-out:empty:before {
  content: "\200b";
}

.table-dark .form-control-plaintext {
  color: #fff;
}

/* page ----------------------------------------------------------- */
tobago-page {
  display: block;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.tobago-page-overlay {
  display: table;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* TODO: better z-index strategy */
  z-index: 500;
  /* less than the bootstrap navbar */
  transition-delay: 1s;
  transition-duration: 250ms;
  transition-property: opacity;
  opacity: 0;
}

.tobago-page-overlay-timeout {
